/* for app and utility*/
body {
    padding: 0 !important;
    margin: 0 !important;
    box-sizing: border-box;
    background-color: aqua;
}

.container {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

button{
    cursor: pointer;
}

/* heading */
.heading{
    padding: 0.5em 1em;
    display: flex;
    justify-content: space-between;
}

h1 {
    margin: 0;
}

.navBtn{
    background-color: rgb(235, 241, 226);
    border-radius: 10px;
    text-align: center;
    border: 1px solid;
    cursor: pointer;
}

.navBtns{
    display: flex;
    gap: 1rem;
}

.mode{
    margin: auto;
    font-size: xx-large;
    background-color: unset;
    border: 0;
}

/* for Resume */

.resumes-container{
    display: flex;
    flex-direction: column;
    row-gap: 2rem;
}

.resume {
    border: 3px solid grey;
    padding: 0 10px;
    margin: 4px 0;
}

.resume ul{
    float: right;
}

.deleteItem {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 1rem;
}
.deleteItem span{
    cursor: pointer;
}


.foot-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem 1rem;
}
.crud {
    display: flex;
    gap: 1rem;
}
.crud span {
    cursor: pointer;
}


/* div.d-grid {
    display: grid !important;
    grid-template-columns: 0.5fr 2fr 0.5fr !important;
} */

.align-items-center {
    align-items: center;
}


/* for Form */
.resumeForm {
    padding-inline: 2em;
}

.resumeForm h2 {
    text-align: center;
    margin-block: 0 20px;
}

.resumeForm label {
    width: 80%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin-bottom: 3rem;
    align-items: center;
}

.resumeForm label input {
    padding-block: 4px;
}

.resumeForm label textarea {
    height: 60px;
}

.resumeForm label button {
    margin-inline: 2em;
    cursor: pointer;
}

.submitBtn {
    display: block;
    margin: 0 auto;
    padding: 0.5em 2em;
    border-radius: 3px;
    background-color: black;
    color: white;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.2s ease;
}

.submitBtn:hover {
    background-color: rgb(54, 47, 47);
}

/* Theme Mode */
.dark{
    background-color: black !important;
    color: #ebd9d9 ;
}

.input-dark{
    background-color: #ebd9d9 ;
}

.submitBtn-dark{
    background-color: rgb(54, 47, 47) !important;
}
.submitBtn-dark:hover{
    background-color: black !important;
}
